  transition: 0.3s;
  width: 9%;
  height: 6.95rem;
  position: relative;
  margin-left: 1%;
  display: flex;
  align-items: center;
}
span {
  margin-top: 20px;
  display: block;
  text-align: center;
}
img {
  transition: all 0.6s ease-out;
}
.imgBox {
  transition: all 0.3s ease-out;
  height: 7rem;
  width: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #2f7ccd;
  border-radius: 50% 50%;
}
li:hover .imgBox {
  background: linear-gradient(to bottom, #08e4e7, #2f7ccd);
  transition: all 0.3s ease-out;
}
img {
}
li,
li:hover {
  transition: all 0.5s ease;
}
</style>

