// In the search component's template
<template>
  <div>
    <div>

     
  <div class="box">
    <img src="../assets/back.jpg" alt="" />


      <label for="title">생물검색 </label>
      <input id="title" type="text" v-model="value" />
      <button @click="search()">검색</button>
    </div>
  </div>
  </div>
</template>

<script>
// In the search component's script
import jdata from "../species.json";

export default {
  props: ["species"],
  data() {
    return {
      message: false,
      msgtext: "검색 데이터가 없습니다",
      value: "",
    };
  },
  methods: {
    search: function () {
      let text = this.value;
      if (text) {
        let results = jdata.filter(function (element) {
          return element.대표국명.includes(text);
        });
        this.message = true;
        this.msgtext =
          results.length == 0 ? "검색 데이터가 없습니다" : results.length + "개가 검색되었습니다";
        this.$emit("search-results", results);
      } else {
        alert("검색어를 입력해주세요!");
      }
    },
  },
};
</script>







